/* src/App.css */
.AppContainer {
  padding: 20px;
  margin: 20px;
}

.Records {
  padding: 20px;
}

.Record {
  display: flex;
  justify-content: space-between;
  align-items: center; /* Ensures items are centered vertically */
}

.UserRecords {
  margin-bottom: 20px; /* Adds spacing between different users' records */
}

.UserRecordsHeader {
  display: flex;
  justify-content: space-between;
  align-items: center; /* Ensures items are centered vertically */
  padding-bottom: 5px; /* Adds spacing below user header */
}

.UserSquareHeader,
.UserSquareFooter {
  display: flex;
  justify-content: space-between;
  align-items: center; /* Ensures items are centered vertically */
}

.RecordName {
  width: 30%;
}

.RecordTitle {
  width: 40%;
}

.RecordAmount {
  width: 30%;
}